// 设置cookie
function SetCookie(cname, cvalue, exdays) {
    if (exdays) {
        var d = new Date();
        d.setTime(d.getTime() + exdays * 24 * 60 * 60 * 1000);
        var expires = "expires=" + d.toGMTString();
        document.cookie = cname + "=" + cvalue + "; " + expires;
    } else {
        document.cookie = cname + "=" + cvalue + "; ";
    }
}
// 获取cookie
function GetCookie(cname) {
    var name = cname + "=";
    var ca = document.cookie.split(';');
    for (var i = 0; i < ca.length; i++) {
        var c = ca[i].trim();
        if (c.indexOf(name) == 0) return c.substring(name.length, c.length);
    }
    return "";
}
// 删除cookie
function DelCookie(cname) {
    document.cookie = cname + "=; expires=Thu, 01 Jan 1970 00:00:00 GMT";
}
// localstorage写入本地缓存
function SetLocalS(key, value) {
    localStorage.setItem(key, value);
}
// localstorage获取本地缓存
function GetLocalS(key, value) {
    var res = localStorage.getItem(key);
    if (res && res != 'null') return res;else return false;
}
// localstorage删除本地储存
function DelLocalS(key) {
    localStorage.removeItem(key);
}
// localstorage清空本地储存
function ClearLocalS() {
    localStorage.clear();
}
// 获取参数值
function GetParams(params) {
    var obj = { key: [], value: [] };
    for (var i in params) {
        obj.key.push(i);
        obj.value.push(params[i]);
    }
    return obj;
}
// 加密（简单模拟）
function Encrypt(value) {
    var code = '';
    for (var i = 0; i < value.length; i++) {
        var r = value.charCodeAt(i);
        code += String.fromCharCode(r + 2);
    }
    // 对字符串进行特殊字符编码，分号（;）、逗号（,）、等号（=）以及空格问题
    return escape(code);
}
// 解密（简单模拟）
function Decrypt(value) {
    // 对字符串进行特殊字符解码，分号（;）、逗号（,）、等号（=）以及空格问题
    value = unescape(value);
    var correct = '';
    for (var i = 0; i < value.length; i++) {
        var r = value.charCodeAt(i);
        correct += String.fromCharCode(r - 2);
    }
    return correct;
}

